// Importe des composantes de la librairie react-bootstrap
import { Container, Row } from 'react-bootstrap';

// Importe des images
import imagePrincipale from '../images/about_image1.png';
import image2 from '../images/about_image2.png';

// Importe une composante
import PrquoiSection from './PrquoiSection';


const About = () => {
    return ( 
        <Container className='aPropos'>

            {/* Image principale */}
            <div className="image">
                <Row>
                    <img src={imagePrincipale} alt="Image principale de la page  propos" className='w-100' role='img' />
                </Row>
            </div>

            <div className="about w-75 mx-auto pb-2 pt-3">
                <div className='equipe ps-3 pe-3'>

                    {/* Titre de la page */}
                    <div className='row'>
                        <h2> PROPOS</h2>
                        <hr className='ps-3 pe-3'></hr>
                    </div>
                </div>

                {/* A propos de la compagnie */}
                <div className="aboutUsContainer">
                    <div className="col-sm-8 col-lg-4 p-4">
                        <img src={image2} alt="Image d'exercice" role='img' className='w-100'/>
                    </div>

                    {/* Description */}
                    <div className="col-sm-12 col-lg-8 p-4 ps-2">
                        <p>Chez NATURA physio, nous sommes fiers d'avoir des physiothrapeutes et massothrapeutes expriments. 
                            <br/><br/>
                            Notre quipe valueront et traiteront votre blessure dans une atmosphre agrable et dtendue. 
                            Nous offrons une grande varit de services et de traitement uniques  nos cliniques, y compris la 
                            thrapie manuelle, l'acupuncture, la massothrapie, le dry needling et l'aquathrapie. 
                            <br/><br/>
                            Notre mission est d'tre le meilleur tablissement de physiothrapie et de radaptation de la ville 
                            et de rpondre  vos besoins et vous offrant des services adapts. 
                            <br/><br/>
                            L'atmosphre dtendue de nos cliniques offre un environnement idal pour se remettre d'une blessure. 
                            <br/><br/>
                            Notre but est de faire en sorte que nos clients se sentent  l'aise afin que notre quipe discute 
                            de leurs proccupations avec eux, afin de nous assurer que leurs attentes sont satisfaites.
                            <br/></p>
                        <button className='btn' role="button" aria-placeholder='Bouton qui mne sur la page de rservation'>Rservez un rendez-vous</button>
                    </div>
                </div>
            </div>
            
            {/* Section de widgets */}
            <PrquoiSection/>


            <div className='videoAboutUs'>
                <div className="video">**VIDO  venir dans la version finale</div>
            </div>
        
        </Container>
     );
}
 
export default About;