// Import de react-bootstrap
import { Col, Container, Row } from "react-bootstrap";

// Import images et icones
import medal from "../images/medal.png"
import people from "../images/people.png";
import locationBlue from "../images/location.png";

const Accomplissement = () => {
    return ( 
        <div className="iconSection mb-3">
            <Container>
                <Row className="icons text-center">
                    <Col>
                        <img src={medal} alt="Icne d'une mdaille" role="img"/>
                        <p className="pt-2">Clinique trs bien cote pour deux annes conscutives </p>
                    </Col>

                    <Col>
                        <img src={locationBlue} alt="Icne qui dmontre un emplacement" role="img" />
                        <p className="pt-2">Emplacement facile d'accessibilit pour tous </p>
                    </Col>

                    <Col>
                        <img src={people} alt="Icne de personnes pour l'acceptation" role="img" />
                        <p className="pt-2">Nous acceptons tous le monde</p>
                    </Col>
                </Row>
            </Container>      
        </div>

     );
}
 
export default Accomplissement;