// Importe de la librairie react
import {Container, Modal } from "react-bootstrap";
import React, { useEffect } from "react";
import { useState } from "react";

// Fichiers
import { data_equipe_physio } from "../../Equipe/physio_data";
import { time_slots_data } from "../Page1/time_slots_data";

const Confirmation = ({setFormData, formData}) => {

    // Variable
    const [dateRdv, setDateRdv] = useState([]);

    /**
     * Cette fonction est appel afin d'afficher la date et l'heure slectionne
     * par l'utilisateur sur la page de rservation. Elle convertit les valeurs
     * entres afin de crer une date qui correspond  ces valeurs afin d'afficher
     * la date et l'heure sous la forme : Lundi 10 Juin 2023  11h30.
     * @param {*} tempsSelectionnee
     */
    function getDateAndTime(dateChoisi, temps) {

        let newDate = new Date(dateChoisi);
        let date = newDate.getDate();
        let month = newDate.getMonth();
        let year = newDate.getFullYear();
        let newTime = '';
        let pro = '';

        // Constantes pour les jours de la semaine et les mois dans une anne
        const jours = ["Dimanche","Lundi","Mardi","Mercredi","Jeudi","Vendredi","Samedi"];
        const mois = ["Janvier", "Fvrier", "Mars", "Avil", "Mai", "Juin", "Juillet", "Aot", "Septembre", "Octobre", "Novembre", "Dcembre"];

        time_slots_data.find((data) => { if ((data.id) == temps) { newTime = data.value}})

        // Trouve le professionel
        data_equipe_physio.find((data) => { if ((data.id-1) == formData.professionel) { pro = data.nom}})
        
        // Insre la date et heure sous forme: Lundi 10 Juin 2023  11h30.
        let tmpText = jours[newDate.getDay()] + " " + date + " " + mois[month] + " " + year + "  " + newTime + " avec " + pro;
        setDateRdv(tmpText);
        console.log("la date est: " + tmpText);
    }

    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    useEffect(() => {
        getDateAndTime(formData.dateRdv, formData.heure);

    }, [dateRdv]);

    return (
        <div>
            <Container className="reservation-confirmation">
                
                {/* Titre de la page */}
                <div className="titre-reservation-confirmation">
                    <h2>CONFIRMATION</h2>

                </div>

                {/* Message de confirmation de la rservation */}
                <div className="details-reservation-confirmation">
                    <p>
                        Bonjour {formData.prenom},
                        <br />
                        Votre rservation a t reue et confirme.
                        <br/>
                        Une confirmation de votre rservation vous a t envoye  l'addresse courriel suivante : {formData.courriel}
                    </p>
                    <h2>DTAILS</h2>
                    <p>
                        Le rendez aura lieu le {dateRdv}.
                    </p>

                    {/* Reponses aux questions si c'est un nouveau patient */}
                    {(formData.estNouveauPatient == true) ? 
                    <div>
                        <p>Voici vos rponses aux questions suivantes. </p>
                        <p className="mt-3 mb-0">Veuillez brivement expliquer vos problmes.</p>
                        <p className="text-muted">{formData.explicationProbleme}</p>
                        <p className="mt-3 mb-0">Quand et o avez-vous remarqu vos douleurs pour la premire fois?</p>
                        <p className="text-muted">{formData.remarqueDouleur}</p>
                    </div>
                    : null
                    }
                    <p>Si vous avez  apporter des changements ou  annuler votre rservation plus tard, veuillez
                        <br/>
                        nous rejoindre au 613-109-9876.
                        <br/>
                        Pour annuler votre rservation maintenant :
                    </p>

                    <button className="btn" onClick={handleShow} type="button">Annuler le rendez-vous</button>

                    <br/><br/><br/>

                    <p>Dans le cas d'une annulation  moins de 24h d'avis, des fraits seront exigs.  </p>
                </div>

                <a href="/"><button className='btn' role="button">Retour  la page d'accueil</button></a>
                
                {/* Si l'utilisateur annule son rendez-vous, le modal est affich */}
                <Modal
                show={show}
                onHide={handleClose}
                backdrop="static"
                keyboard={false}
                size="lg"
                aria-labelledby="contained-modal-title-vcenter"
                centered
                >
                    <Modal.Header >
                    <Modal.Title>L'annulation de votre rendez-vous a t effectu avec succs</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        Bonjour {formData.prenom},
                        <br/>
                        Un courriel vous a t envoy confirmant l'annulation de votre rservation  l'adresse courriel suivante : {formData.courriel}
                        <br/>
                        Si vous avez des questions, n'hsitez pas  nous rejoindre par tlphone : 613-109-9876
                        <br/><br/>
                        - quipe NATURA physio
                    </Modal.Body>
                    
                    <Modal.Footer className="justify-content-start">
                        <a href="/"><button className="btn" onClick={handleClose} role="button">Retour  la page d'accueil</button></a>
                    </Modal.Footer>
                </Modal>
            </Container>
        </div>
     );
}

export default Confirmation;